import React from 'react';

import Card from '../../components/Card';
import AppContext from '../../context';
import Info from '../../components/Info';
import styles from './Favorites.module.scss';

function Favorites() {
  const { favoriteItems, addToFavoriteItemsClick } = React.useContext(AppContext);

  return (
    <div className="content p-40">
      <div className="d-flex align-center justify-between mb-40">
        <h1>Мои закладки</h1>
      </div>
      {favoriteItems.length > 0 ? (
        <div>
          {favoriteItems.map((item, index) => (
            <Card
              key={index}
              isAddedToFavoriteItems={true}
              addToFavorite={addToFavoriteItemsClick}
              {...item}
            />
          ))}
        </div>
      ) : (
        <Info
          width="75px"
          heigth="75px"
          title="Люимых товаров нет"
          description="Добавьте товар"
          image="img/favorites-empty.png"
        />
      )}
    </div>
  );
}

export default Favorites;
